.c-header {
    position: relative
}

@media (max-width: 699px) {
    .c-header {
        height:80vh;
        min-height: 340px;
        margin: 10px;
    }
}

@media (min-width: 700px) and (max-width:999px) {
    .c-header {
        height:90vh;
        min-height: 380px;
        margin: 10px;
    }
}

@media (min-width: 1000px) {
    .c-header {
        height:100vh;
        min-height: 46.875rem
    }
}


