.o-layout {
    list-style: none;
    margin: 0;
    padding: 0
}

.o-layout_item {
    display: inline-block;
    font-size: 1rem;
    padding-left: 0;
    vertical-align: top;
    width: 100%
}

.u-label {
    font-size: 1.5rem;
    font-weight: 600;
    text-transform: uppercase
}

.u-float-left {
    float: left!important
}

.u-float-right {
    float: right!important
}

.u-text-center {
    text-align: center!important
}

.u-text-left {
    text-align: left!important
}

.u-text-right {
    text-align: right!important
}
